<template>
    <div>
        <input type="file" name="file" ref="myFileRef">
        <button type="button" @click="uploadFile()">Upload</button>
    </div> 
</template>
<script>
import axios from 'axios';
export default {
    name:'uploads',
    methods:{
        uploadFile: function(){
            let file = this.$refs.myFileRef.files[0];

            if(!file){
                console.error("no file selected")
                return;
            }

            let formData = new FormData()

            formData.append('file',file)

            return axios.post(
                'http://localhost:3000/uploads',
                formData,
                {
                    headers: {
                        'Content-Type':'multipart/form-data'
                    }
                }
            ).then(response =>{
                console.info("file uploaded successfuly")
            })
            .catch(error =>{
                console.error("file upload faild",error)
            });

        }
    }
    
}
</script>
